	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Vacation Plans","date":"17 May 2001","slug":"/2001-05-17-vacation-plans/","tags":"Y2001"},"html":"<p>I won't use the word 'might' anymore. All three of us will drive down to Amsterdam next weekend. I can't remember the last pleasure trip we took. Before Mama's pregnancy, I'm sure, so it's been at least 2 years. (Except, of course, for our road trip to  <a href=\"http://shanson.edtthispage.com/2000/10/21\">Odessa</a> while in Minnesota last fall. But that wasn't an overnight.)</p>\n<p>I was last in Amsterdam in 1985 on our honeymoon of sorts. We were poor students, and couldn't afford a hotel, only a boat hostel which turned out to be owned by some cult. </p>\n<p>In August we'll go to Sylt, on the North Sea coast, for a week. Mama's employer owns an an apartment for employee use. Sylt has the reputation of being overrun by millionaire snobs in the summer, but the millionaires are pretty easy to avoid, if you're careful.</p>"},"next":{"fields":{"title":"Road Trip","date":"16 May 2001","slug":"/2001-05-16-road-trip/"}},"previous":{"fields":{"title":"The Mug Game","date":"19 May 2001","slug":"/2001-05-19-the-mug-game/"}}},{"node":{"fields":{"title":"Road Trip","date":"16 May 2001","slug":"/2001-05-16-road-trip/","tags":"Y2001"},"html":"<p>Mama managed to replan her appointments for next weekend, so we might all three be taking a road trip to Amsterdam for the \"Scripting News\" dinner, and to see the sights and some <a href=\"http://andrea.editthispage.com\">nice </a><a href=\"http://spicynoodles.com\">people</a>.</p>"},"next":{"fields":{"title":"Tired boy","date":"14 May 2001","slug":"/2001-05-14-tired-boy/"}},"previous":{"fields":{"title":"Vacation Plans","date":"17 May 2001","slug":"/2001-05-17-vacation-plans/"}}},{"node":{"fields":{"title":"Tired boy","date":"14 May 2001","slug":"/2001-05-14-tired-boy/","tags":"Y2001"},"html":"<p>I don't know how we managed it, but we wore Christopher completely out yesterday. He fell asleep on the living room floor before we could take him to bed. Too much sandbox, swing set, and Bobby Car!</p>\n<p>We try to avoid 'potty talk' here at PapaScott, but now for the second day in a row, Christopher has come to me when his diaper was full, more or less asking to be changed. Maybe he's implying that I sometimes forget to check.</p>"},"next":{"fields":{"title":"42","date":"13 May 2001","slug":"/2001-05-13-42/"}},"previous":{"fields":{"title":"Road Trip","date":"16 May 2001","slug":"/2001-05-16-road-trip/"}}}],"pathPrefix":"","first":false,"last":false,"index":1004,"pageCount":1101}}

